const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.last')
//‰∏ÄËøõÂÖ•ÂΩìÂâçÈ°µÈù¢ÁöÑÊó∂ÂÄôËé∑ÂèñxÂÄºÔºå
const x = localStorage.getItem('x')
const xObject = JSON.parse(x) //JSON.parseÊääÂ≠óÁ¨¶‰∏≤xÂèòÊàêÂØπË±°xObject
console.log('x:' + x)
console.log('xObject:' + xObject)
//ÂàùÂßãÂåñÁöÑÊó∂ÂÄôÔºåxÊúâÂÄºÁöÑÊó∂ÂÄôÔºåxObjectÊòØÁ©∫ÔºåÊâÄ‰ª•Áî® ||Êù•Ëé∑ÂèñÂêéÈù¢ÁöÑÊï∞ÁªÑÂÄº
const hashMap = xObject || [//Â£∞Êòé‰∏Ä‰∏™ÂÖ®Â±ÄÂèòÈáè
    {logo:'A',logoType:'text',url:'https://www.acfun.cn'},
    {logo:'B',logoType:'image',url:'https://bilibili.com'},
]

const simplifyUrl = (url) =>{
    return url.replace('https://','')
           .replace('http://','')
           .replace('www.','')
           .replace(/\/.*/,'')
}

const render = () => {
    hashMap.forEach(node=>{
        const $li = $(`
                <li>
                    <a href="${node.url}" >
                        <div class="site">
                            <div class="logo">${simplifyUrl(node.url)[0].toUpperCase()}</div>
                            <div class="link">${simplifyUrl(node.url)}</div>
                        </div>
                    </a>
                </li>
        `).insertBefore($lastLi)
    })
}

render()

$('.addButton').on('click',()=>{
    console.log(1)
    let url=window.prompt("‰Ω†ÊÉ≥Âπ≤Âï•Ôºü")
    if(url.indexOf('https')!==0){
        url = 'https://' + url
    }
    hashMap.push({
        logo:simplifyUrl(url)[0].toUpperCase(),
        logoType:'text',
        url:url
    })
    $siteList.find('li:not(.last)').remove()
    render()
});

//ÂÖ≥Èó≠È°µÈù¢ÁöÑÊó∂ÂÄôÊääÂΩìÂâçÁöÑhashMapÂ≠òÂà∞xÈáåÈù¢
window.onbeforeunload = () =>{
    console.log(`È°µÈù¢Ë¶ÅÂÖ≥Èó≠‰∫Ü`)
    //ÊääÂØπË±°ÂèòÊàêÂ≠óÁ¨¶‰∏≤Áî®JSON.stringfy()
    const string = JSON.stringify(hashMap)
    /*Êü•Áúã‰∏ãÈù¢Âõõ‰∏™ÂÄºË¶ÅÂãæÈÄâpreserve log*/
    // console.log(hashMap)
    // console.log(typeof hashMap) //object
    // console.log(string)
    // console.log(typeof string) //Á±ªÂûãÊòØstring

    //‰∫ßÁúãlocalStorageÁöÑÂÄºÔºåË¶ÅÁúãÊéßÂà∂Âè∞‰∏≠Application
    //‰∏≠ÁöÑStorage‰∏≠ÁöÑlocal storageÔºåÂØπÂ∫îxÂíåstring
    localStorage.setItem('x',string)
}